            成考答题这几点要注意
            <br />
            <br />答题卡管理:考生填写考号、姓名不能超出答题区域,包括所修改的内容,不能在图象定位点(黑
            方块)附近涂写做标记,保持卡面清洁,不能折叠、弄破
            二、条形码管理:考生在監考教师的指导下,将条形码正确粘贴在指定位置,不能在条形码区乱涂乱划。
            <br />
            <br />三、用笔
            <br />
            <br />1、选择题必须用2B铅笔填写
            <br />
            <br />2、非选择题必须用0.5毫米黑色水的签字笔,不得用铅笔、红色和蓝色水的钢笔或圆珠笔作答(以保
            证答题卡扫描的清晰、完整、准确)
            <br />
            <br />3、作图题可先用铅笔绘出,确认后再用签字笔或钢笔描清楚
            <br />
            <br />4、字迹清楚,字距行距适当。
            <br />
            <br />祝各位同学都能考取好成绩,贵州成考网提示您:成考不复习,考试泪两行
            <br />
